import React, { useState } from "react";
import "react-dyn-tabs/style/react-dyn-tabs.css";
import "./scoped-themes/card.css";
import "./scoped-themes/classic.css";
import "./scoped-themes/basic.css";
import "./scoped-themes/bootstrap.css";
import "./App.css";
import MainComponent from "./main-component.js";
import ShowCode from "./show-code/showCode.js";
function App() {
  const [scopeSelector, setScopeSelector] = useState("card");
  const onChangeTheme = (e) => {
    setScopeSelector(e.target.value);
  };
  return (
    <>
      <div id="header">
        <a
          href="https://github.com/dev-javascript/react-dyn-tabs"
          target="_blank"
        >
          <h1>react-dyn-tabs</h1>
        </a>
        <div id="theme-container">
          <label htmlFor="theme-selector">Theme : </label>
          <select
            id="theme-selector"
            onChange={onChangeTheme}
            value={scopeSelector}
          >
            <option key="2" value="card">
              card
            </option>
            <option key="1" value="bootstrap">
              bootstrap
            </option>
            <option key="3" value="basic">
              basic
            </option>
            <option key="4" value="classic">
              classic
            </option>
          </select>
        </div>
      </div>
      <a
        href="https://github.com/dev-javascript/react-dyn-tabs"
        target="_blank"
      >
        <img
          id="github-img"
          src="img/forkme_right_red_aa0000.svg"
          alt="Fork me on GitHub"
        />
      </a>
      <div className={scopeSelector}>
        <MainComponent />
      </div>
      <ShowCode theme={scopeSelector}></ShowCode>
      <div id="code-container">
        <pre>
          <code></code>
        </pre>
      </div>
    </>
  );
}

export default App;
