import React, {useState} from 'react';
import {getCode} from './service.js';
import './show-code.css';
export default function ({theme}) {
  const [showCode, setShowCode] = useState(false);
  const code = getCode(theme);
  const checkboxOnChange = (e) => {
    setShowCode(!showCode);
  };
  return (
    <>
      <div className="show-code">
        <label htmlFor="show-code-checkbox">show code</label>
        <input type="checkbox" id="show-code-checkbox" value={showCode} onChange={checkboxOnChange}></input>
      </div>
      {showCode && <pre>{code}</pre>}
    </>
  );
}
